import { authConfig } from "./config/auth.config"; 
import NextAuth from "next-auth";
import Credential from "next-auth/providers/credentials";
import { NextResponse } from "next/server";

export const { handlers, signIn, signOut, auth } = NextAuth({
  ...authConfig,
  providers: [
    Credential({
      authorize: async (credentials) => {
        if (process.env.NODE_ENV === "development") {
          console.log("ðŸš©credentialsðŸš©", credentials); // Avoid logging sensitive data in production
        }

        try {
          const existUser = await fetch(
            `http://localhost:4000/users?email=${credentials.email}`
          );
          const userData = await existUser.json();

          if (!userData || userData.length === 0) {
            throw new Error(
              JSON.stringify({
                errors: "User not found",
                status: false,
              })
            );
          }

          return { user: userData[0].name, email: userData[0].email };
        } catch (error) {
          console.error("Error during authentication:", error);
          throw new Error(
            JSON.stringify({
              errors: "An error occurred during authentication",
              status: false,
            })
          );
        }
      },
    }),
  ],
  callbacks: {
    signIn({ user, account, credentials }) {
      return credentials.callbackUrl; 
    },
  },
});
